.main-container {
  margin: 0 100px;
}

.index__top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 80px 100px;
  color: #2E2E2E;
  font-family: 'Inter', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 17px;
  display: flex;
  align-items: center;
}

.index__top a {
  text-decoration: none;
  color: #2E2E2E;
}

.index__top-back {
  display: flex;
  align-items: center;
}

.index__top-back--arrow {
  display: flex;
  background-image: url("data:image/svg+xml,%3Csvg width='35' height='7' viewBox='0 0 35 7' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M3.075 6.875L0.075 3.875C-0.025 3.675 -0.025 3.275 0.075 3.075L3.075 0.075C3.275 -0.025 3.675 -0.025 3.875 0.075C3.975 0.275 3.975 0.675 3.875 0.775L1.875 2.975H34.975V3.975H1.875L3.875 6.175C3.975 6.375 3.975 6.675 3.875 6.875C3.675 6.975 3.275 6.975 3.075 6.875Z' fill='%232E2E2E' /%3E%3C/svg%3E");
  margin-right: 13px;
  width: 36px;
  height: 7px;
  background-repeat: no-repeat;
}

.index__top-center {
  position: relative;
  font-family: 'Manrope', sans-serif;
  font-style: normal;
  font-weight: normal;
}

.index__top-center h1 {
  font-style: normal;
  font-weight: normal;
  font-size: 72px;
  line-height: 98px;
  display: flex;
  align-items: center;
  text-align: center;
  letter-spacing: 0.25em;
  text-transform: uppercase;
  color: #2E2E2E;
}

.index__top-center--gold {
  position: absolute;
  bottom: -50px;
  font-weight: 700;
  font-size: 14px;
  line-height: 19px;
  letter-spacing: .35em;
  color: #cea78f;
  margin: 0 auto;
  left: 41px;
  text-transform: uppercase;
}

.index__top-forward {
  display: flex;
  align-items: center;
}

.index__top-forward--arrow {
  display: flex;
  background-image: url("data:image/svg+xml,%3Csvg width='36' height='7' viewBox='0 0 36 7' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M31.9 0.1L34.9 3.1C35.1 3.3 35.1 3.6 34.9 3.8L31.9 6.8C31.7 7 31.4 7 31.2 6.8C31 6.6 31 6.3 31.2 6.1L33.2 3.9H0V2.9H33.1L31.1 0.7C30.9 0.5 30.9 0.2 31.1 0C31.3 0 31.7 0 31.9 0.1Z' fill='%232E2E2E' /%3E%3C/svg%3E");
  margin-left: 13px;
  width: 36px;
  height: 7px;
  background-repeat: no-repeat;
}

.index__top.fixed {
  margin-top: 70px;
}

.about-main-slider {
  position: relative;
  top: 0;
  left: 0;
  max-height: calc(100vh - 70px);
}

.about-main-slider__img {
  position: absolute;
  width: 100%;
  display: none;
  object-fit: cover;
  overflow: hidden;
  height: 100%;
  top: 50%;
  transform: translate(0, -50%);
  background-size: cover;
}

@keyframes showBlock {
  0% {
    opacity: 0.2;
  }

  100% {
    opacity: 1;
  }
}

.about-main-slider__img.active {
  display: block;
  animation: showBlock 0.3s linear forwards;
}

.about-main-slider__arrow-next {
  position: absolute;
  width: 110px;
  height: 100%;
  right: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

.about-main-slider__arrow-next svg {
  height: 36px;
}

.about-main-slider__arrow-next.hiden {
  display: none;
}

.about-main-slider__arrow-prev {
  position: absolute;
  width: 110px;
  height: 100%;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

.about-main-slider__arrow-prev svg {
  height: 36px;
}

.about-main-slider__arrow-prev.hiden {
  display: none;
}

.under-slider-info {
  margin: 20px 0 50px;
  display: inline-flex;
  justify-content: space-between;
  width: 100%;
  font-size: 14px;
  line-height: 19px;
  color: #808080;
}

.under-slider-info .bread-crumbs {
  position: relative;
  display: flex;
}

.under-slider-info .about-main-slider__info {
  position: relative;
  display: flex;
}

.under-slider-info .about-main-slider__info--slash {
  margin: 0 10px;
}

.under-slider-info .about-main-slider__info.hiden {
  display: none;
}

.about-info {
  display: grid;
  grid-template-columns: 2fr 1fr 1fr;
  grid-template-rows: repeat(3, auto);
  font-family: 'Manrope', sans-serif;
  font-style: normal;
  font-weight: normal;
  color: #2E2E2E;
}

.about-info h2 {
  grid-column: 1;
  grid-row: 1;
  font-size: 32px;
  line-height: 44px;
  font-weight: 600;
  position: relative;
}

.about-info h2 a {
  text-decoration: none;
  color: #CEA78F;
}

.about-info h2 svg {
  position: relative;
  bottom: 7px;
}

.about-info__left-text {
  grid-column: 1;
  grid-row: 2;
  display: flex;
  align-items: flex-end;
  font-size: 18px;
  line-height: 25px;
}

.about-info__right-text {
  grid-column-start: 2;
  grid-column-end: 4;
  grid-row-start: 1;
  grid-row-end: 3;
  font-size: 14px;
  line-height: 24px;
  font-family: 'Inter', sans-serif;
  color: #808080;
}

.about-info__1 {
  grid-row: 3;
  grid-column: 2;
  margin-top: 60px;
}

.about-info__1-first-string {
  display: inline-flex;
  align-items: baseline;
}

.about-info__1-first-string div {
  font-size: 72px;
  line-height: 98px;
}

.about-info__1 p {
  font-size: 14px;
  line-height: 22px;
  color: #808080;
  font-family: 'Inter', sans-serif;
  max-width: 70%;
}

.about-info__2 {
  grid-row: 3;
  grid-column: 3;
  margin-top: 60px;
}

.about-info__2 div {
  font-size: 72px;
  line-height: 98px;
  color: #CEA78F;
}

.about-info__2 p {
  font-size: 14px;
  line-height: 22px;
  color: #808080;
  font-family: 'Inter', sans-serif;
  max-width: 70%;
}

.about-bar {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-column-gap: 32px;
  font-family: 'Inter', sans-serif;
