.about-awards {
  font-family: 'Inter', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 17px;
}

.about-awards h2 {
  font-family: 'Manrope', sans-serif;
  font-weight: 600;
  font-size: 32px;
  line-height: 44px;
  color: #2E2E2E;
}

.about-awards__awards-count {
  font-size: 14px;
  line-height: 17px;
  text-align: right;
  color: #808080;
}

.about-awards__bold {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-column-gap: 32px;
  font-weight: 600;
  text-transform: uppercase;
  height: 80px;
  align-items: center;
}

.about-awards__award {
  display: none;
  position: relative;
}

.about-awards__award.active {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-column-gap: 32px;
  height: 80px;
  align-items: center;
  cursor: pointer;
  border-bottom: 1px solid #C7C7C7;
}

.about-awards__award.active a.about-awards__award--arrow {
  position: absolute;
  background-image: url("data:image/svg+xml,%3Csvg width='36' height='7' viewBox='0 0 36 7' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M31.9 0.1L34.9 3.1C35.1 3.3 35.1 3.6 34.9 3.8L31.9 6.8C31.7 7 31.4 7 31.2 6.8C31 6.6 31 6.3 31.2 6.1L33.2 3.9H0V2.9H33.1L31.1 0.7C30.9 0.5 30.9 0.2 31.1 0C31.3 0 31.7 0 31.9 0.1Z' fill='%232E2E2E' /%3E%3C/svg%3E");
  right: 50px;
  width: 36px;
  height: 7px;
  background-repeat: no-repeat;
  display: flex;
}

.about-awards__award.active:hover {
  background: #F5F5F5;
}

.about-awards__award.active:hover p {
  padding-left: 30px;
}

.about-awards__award.active:hover a {
  display: flex;
  right: 20px;
}

.about-awards__load-more {
  height: 80px;
  margin: 20px 0;
  font-family: 'Manrope', sans-serif;
  font-weight: bold;
  font-size: 16px;
  line-height: 22px;
  display: flex;
  justify-content: center;
  color: #CEA78F;
}

