.about-staff {
  margin: 110px 0;
  font-family: 'Inter', sans-serif;
  font-style: normal;
  font-weight: normal;
  color: #808080;
}

.about-staff h2 {
  display: flex;
  justify-content: center;
  font-family: 'Manrope', sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 32px;
  line-height: 44px;
  text-align: center;
  margin: 50px 0;
  color: #2E2E2E;
}

.about-staff__letters {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 50px 0;
}

.about-staff__letters i {
  font-size: 20px;
  line-height: 24px;
}

.about-staff__letters span {
  margin: 0 10px;
  height: 1px;
  width: 60px;
  background: #E4E4E4;
}

.about-staff__sortbar {
  font-size: 14px;
  line-height: 17px;
}

.about-staff__sortbar-menu {
  width: 100%;
  border-bottom: 1px solid #E4E4E4;
  height: 40px;
  display: inline-flex;
  align-items: center;
  justify-content: space-between;
}

.about-staff__sortbar-menu div:first-child {
  position: relative;
}

.about-staff__sortbar-menu div:first-child svg {
  position: relative;
  bottom: 2px;
  left: 10px;
}

.about-staff__sortbar-menu span {
  padding-left: 2px;
}

.about-staff__sortbar-letters {
  display: flex;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;
  font-size: 16px;
  line-height: 19px;
  position: relative;
  height: 70px;
}

.about-staff__sortbar-letters div {
  text-transform: uppercase;
  cursor: pointer;
  margin: 0 auto;
  position: relative;
  top: -1px;
}

.about-staff__sortbar-letters div:first-child {
  background: #F5F5F5;
  border: 1px solid #DFDFDF;
  box-sizing: border-box;
  border-radius: 16px;
  padding: 5px 10px;
}

.about-staff__sortbar-letters div:first-child:hover {
  padding: 5px 10px;
  border: 1px solid #DFDFDF;
  text-shadow: none;
  color: inherit;
}

.about-staff__sortbar-letters div:hover {
  padding-top: 25px;
  border-bottom: 25px solid transparent;
  border-top: 1px solid #000;
  text-shadow: 0 0 0.65px #2e2e2e, 0 0 0.65px #2e2e2e;
  color: #2e2e2e;
}

.about-staff__employees {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 32px;
  font-family: 'Manrope', sans-serif;
  font-style: normal;
}

.about-staff__employee {
  display: none;
  position: relative;
}

.about-staff__employee.active {
  display: inline-block;
  overflow: hidden;
  font-family: 'Manrope', sans-serif;
  font-style: normal;
  font-weight: normal;
}

.about-staff__employee.active img {
  display: block;
  transition: 0.4s;
}

.about-staff__employee.active img:hover {
  transform: scale(1.17);
}

