}

.about-bottom-info .about-left-info {
  grid-column: 1;
  font-family: 'Inter', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 24px;
  color: #808080;
}

.about-bottom-info .about-accordion {
  grid-column: 2;
  width: 100%;
  font-family: 'Inter', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 24px;
  color: #808080;
}

.about-bottom-info .about-accordion .about-accordion__steps {
  width: 100%;
  height: 40px;
  padding: 10px;
  border-bottom: 1px solid #C7C7C7;
  align-items: center;
  text-align: right;
  display: flex;
  justify-content: flex-end;
}

.about-bottom-info .about-accordion .accordion {
  position: relative;
  height: 80px;
  text-align: center;
  background-color: #fff;
  cursor: pointer;
  padding: 18px 18px 18px 40px;
  width: 100%;
  text-align: left;
  border: none;
  outline: none;
  transition: 0.4s;
  border-bottom: 1px solid #C7C7C7;
  font-family: 'Manrope', sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 19px;
  color: #2E2E2E;
}

.about-bottom-info .about-accordion .accordion .number {
  position: absolute;
  left: 0;
  font-family: 'Inter', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 17px;
  color: #808080;
}

.about-bottom-info .about-accordion .accordion:after {
  content: '\002B';
  color: #000;
  font-weight: bold;
  float: right;
  font-size: 20px;
  margin-left: 5px;
}

